@import '../../scss/variables';

.cart_container {
  width: $container-width;
  margin: 0 auto;
    max-width: 820px;
    margin: 15px auto;
    h2 {
      display: flex;
      align-items: center;
      font-size: 32px;
      svg {
        position: relative;
        top: -2px;
        width: 30px;
        height: 30px;
        margin-right: 10px;
      }
    }
  }

  .cart {
    &__top {
      display: flex;
      justify-content: space-between;
      align-items: center;

      @media (max-width: 550px){
        flex-direction: column;
        gap: 30px;
      }
    }
  

    &__clear {
      display: flex;
      align-items: center;
      cursor: pointer;
      -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
      span {
        display: inline-block;
        margin-left: 7px;
        color: rgb(236, 12, 12);
        font-size: 18px;
        opacity: 0.7;
      }
      svg {
        width: 20px;
        height: 20px;
         path {
          fill: rgb(236, 12, 12);
          opacity: 0.7;
      }
    }
      span,
      svg,
      path {
        transition: all 0.5s ease-in-out;
      }
  
      &:hover {
       svg path {
        opacity: 1;
          }
        span {
          opacity: 1;
        }
      }
    }
  
    &__bottom {
      margin: 50px 0;
  
      &_details {
        display: flex;
        justify-content: space-between;

        @media (max-width: 650px){
          flex-direction: column;
          gap: 30px
        }
  
        span {
          font-size: 22px;
  
          &:last-of-type {
            b {
              color: $orange;
            }
          }
        }
      }
  
      &_buttons {
        display: flex;
        justify-content: space-between;
        margin-top: 40px;

        @media (max-width: 580px){
          flex-direction: column-reverse;
          gap: 40px;
          align-items: center;
        }
        .go-back-btn {
          display: flex;
          align-items: center;
          justify-content: center;
          width: 210px;
          border-color: darken($color: $gray-line, $amount: 10);
  
          span {
            color: darken($color: $gray-line, $amount: 20);
            font-weight: 500;
            font-weight: 600;
          }
  
          &:hover {
            background-color: darken($color: $gray-line, $amount: 90);
            border-color: darken($color: $gray-line, $amount: 90);
  
            span {
              color: $gray-line;
            }
          }
  
          >svg {
            margin-right: 12px;
            path {
              fill: transparent;
              stroke-width: 2;
            }
          }
        }
  
        .pay-btn {
          font-size: 16px;
          font-weight: 600;
          width: 210px;
          padding: 16px;
        }
      }
    }
  
  }
